.root {
  z-index: 10;
  position: absolute;
  background: linear-gradient(
    135.94deg,
    #f3e7ff 1.59%,
    RGBA(243, 231, 255, 0.4) 50.28%,
    #f3e7ff 100%
  );
  padding: 4px;
  border-radius: 20px;
  width: 280px;
  height: 151px;
  user-select: none;
  filter: drop-shadow(0px 5.31447px 30px RGBA(171 205 255 0.2))
    drop-shadow(0px 0px 30px RGBA(142, 181, 239, 0.3));
  animation-name: emergence;
  animation-duration: 0.3s;
}

.wrapper {
  background: #03090e;
  border-radius: 20px;
  height: 100%;
  padding-top: 10%;
}

.ratio {
  margin: 0;
  line-height: 34px;
  text-align: center;
  color: #7ad25b;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 28px;
}

.hr {
  background-color: fade(#ffffff, 0.2);
  height: 5px;
  width: 23%;
  border-radius: 2px;
  margin: 0 auto;
  margin-bottom: 5px;
  margin-top: 5px;
}

.amount {
  text-align: center;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 20px;
  color: white;
}

.coin {
  position: relative;
  left: 5px;
  top: 5px;
  width: 26px;
  height: 26px;
}

@keyframes emergence {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
